import React, { ReactElement, Dispatch, SetStateAction, useContext, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { isMobile } from '../utils/auth';
import styles from '../css/header.module.css';
import { LoginContext } from '../App';
import { ProfileData } from '../interfaces';

export interface TitlebarProps {
	setID: Dispatch<SetStateAction<string>>
	profile: ProfileData
}

export default function Header(props: TitlebarProps): ReactElement {

	const loggedIn = useContext(LoginContext);
    const buttons = useMemo(() => [
		{
			name: <><span className={'bold'}>Results</span>{'\u200b '}by ScorchChess</>,
			url: '/',
			selected: window.location.pathname !== '/profile' && window.location.pathname !== '/login'
		},
		{
			name: 'ABOUT',
			url: 'https://scorchapp.co.uk'
		}, {
			name: 'DESKTOP',
			url: 'https://beta.scorchapp.co.uk'
		}, {
			name: loggedIn ? (props.profile.username || 'PROFILE') : 'SIGN IN',
			url: loggedIn ? '/profile' : '/login',
			selected: window.location.pathname === '/profile' || window.location.pathname === '/login'
		}
	], [loggedIn, props.profile.username]);

    return (
        <div className={styles.header}>
            <Link 
				to={{pathname: '/', state: { from: window.location.toString() }}}
				className={styles.logoWrapper}
			>
                <img
                    src={process.env.PUBLIC_URL + '/favicon.png'}
                    className={styles.logo}
                    alt='logo'
                />
            </Link>
            {isMobile() ? null : <>
                <div className={styles.buttonContainer}>
                    {buttons.map((b, i) => {
                        return b.url.startsWith('/') ? 
							<Link 
								key={['headerLink', i].join('.')}
								className={[styles.button, b.selected ? styles.selected : ''].join(' ')}
								to={{pathname: b.url, state: { from: window.location.toString() }}}
							>
								{b.name}
							</Link> :
							<a
								key={['headerLink', i].join('.')}
								className={styles.button}
								href={b.url}
								target='_blank'
								rel='noopener noreferrer'
							>
								{b.name}
							</a>                        
                    })}					
                </div>
            </>}
        </div>
    );
}